	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 14 - L’inhibiteur","texte":{"childMarkdownRemark":{"html":"<p>Daryl, Éléa et William étaient arrivés vers 3 heures du matin. William s’était endormi dans la voiture et Éléa et Daryl avaient discuté de ce qui s’était passé de son côté.</p>\n<p>Darakei s’approcha lorsqu’Éléa eu fini de se stationner dans leur entrée. Éléa sorti de la voiture, suivit de William qui se frottait les yeux, il était encore endormi.</p>\n<p>Darakei : Allez, va dans ta chambre.<br>\nWilliam : Oui papa…</p>\n<p>Wiliam rentre dans la maison et va dans sa chambre. Daryl sort de la voiture et ferme la porte. Il se tourne vers Darakei.</p>\n<p>Darakei : Tu veux un café ou quelque chose?<br>\nDaryl : Un café ne serait pas de refus, merci.<br>\nÉléa : Je vais en faire.</p>\n<p>Nous rentrons dans la maison et allons dans la cuisine. Éléa commence à faire les cafés.</p>\n<p>Darakei : Premièrement, où est-ce qu’ils sont?<br>\nDaryl : Ils sont dans une usine désaffectée au sud de Giotry.<br>\nDarakei : Tu serais capable de me pointer l’emplacement sur une carte?<br>\nDaryl : Oui, mais je doute qu’ils soient encore là.<br>\nDarakei : Je vais quand même envoyer des hommes au cas où. Du moins si on peut les retrouver.<br>\nDaryl : C’est vrai.</p>\n<p>Darakei lui tend son portable. Daryl va regarder la carte et indique l’emplacement précis de l’entrepôt. Éléa pose deux cafés près des hommes et commence à boire le sien.</p>\n<p>Éléa : Et on va faire quoi pour William?<br>\nDarakei : Je te laisse t’en charger, entre un ado qui a fait sa petite crise et une douzaine de Nouveaux Humains criminels, le choix n’est pas difficile.<br>\nÉléa : Wow tout un père de famille.</p>\n<p>Éléa quitte la pièce en colère. Darakei soupire pendant que Daryl lui tend la carte.</p>\n<p>Darakei : Merci Daryl.</p>\n<p>Darakei prend son téléphone et envoie les coordonnées à ses troupes qui sont déjà à Giotry. Daryl regarde Darakei.</p>\n<p>Darakei : Ça va, ne me fixe pas comme ça.<br>\nDaryl : Pardon.<br>\nDarakei : Tu te souviens d’une partie des technologies du SIAM?<br>\nDaryl : Oui, surtout du composé chimique de l’inhibiteur.<br>\nDarakei : Sérieux? Tu serais capable de reproduire l’inhibiteur?<br>\nDaryl : Je ne connais que la recette, mais je ne sais pas comment gérer les instruments.<br>\nDarakei : Fournis-moi la recette et je m’occupe du reste.<br>\nDaryl : Si tu as un papier et un crayon.<br>\nDarakei : Je vais te chercher ça.</p>\n<p>Darakei se lève et part à la course chercher ce qu’il faut. Daryl attend et boit une gorgée de café en attendant. Darakei revient un instant plus tard avec des papiers et un crayon.</p>\n<p>Darakei : Voilà.<br>\nDaryl : Merci.</p>\n<p>Daryl prend le papier et le crayon et commence à dessiner un composé chimique assez complexe. Darakei le regarde dessiner, stupéfait.</p>\n<p>Darakei : Évidament!<br>\nDaryl : De?<br>\nDarakei : Continue de dessiner, termine le composé, je sais comment gérer ça.<br>\nDaryl : C’est vous qui voyez Darakei.</p>\n<p>Daryl termine le dessin et ajoute le nom des composantes autour du dessin.</p>\n<p>Darakei : Merci, Daryl, tu ne sais pas à quel point ça va nous aider.<br>\nDaryl : C’était ma tâche de mémoriser ce composant, le trois quarts des employés devaient le connaître par cœur en cas de problème.<br>\nDarakei : Je comprends mieux, pendant que j’y pense, j’aurais besoin d’un échantillon de ton sang pour des tests…<br>\nDaryl : Plus tard, je suis épuisé.<br>\nDarakei : D’accord, je vais te conduire à ta chambre.<br>\nDaryl : Merci Darakei.</p>\n<hr>\n<p>Flynn arrive devant la porte de chambre de Daryl et cogne sauvagement.</p>\n<p>Flynn : Hey Daryl! On a besoin de ton aide pour soulever un truc!</p>\n<p>Il attend un instant devant la porte et cogne de nouveau.</p>\n<p>Flynn : Daryl! Yo!</p>\n<p>Il essaie d’ouvrir la porte et remarque qu’elle est verrouillée.</p>\n<p>Flynn : Putain.</p>\n<p>Flynn bouge sa main devant la porte et fait lever les loquets via son pouvoir avant d’ouvrir la porte. Il entre dans la chambre et voit que la fenêtre est ouverte.</p>\n<p>Flynn : Putain! Il a foutu le camp!</p>\n<p>Il part à la course rejoindre les autres.</p>\n<p>Flynn : Les mecs! Daryl a foutu le camp par la fenêtre de sa chambre!</p>\n<p>L’alerte fut rapidement sonnée et près du trois quarts du groupe sort, à la recherche d’une piste qui pourrait dire vers où Daryl est parti. Pendant ce temps, le reste de l’équipe prépare l’évacuation des lieux et regroupe tout ce qui est important. Les recherches étant infructueuses, le groupe se réunit et part vers un second emplacement.</p>\n<p>Yuu approche son grand frère, l’air inquiète.</p>\n<p>Yuu : On fait quoi s’ils nous trouvent quand même?<br>\nFlynn : Daryl n’a aucun moyen de savoir où nous allons à cause qu’il a entendu est une fausse adresse.<br>\nYuu : Oh! Tu es brillant frérot!<br>\nFlynn : Merci petite sœur.</p>\n<p>Saeko passait proche d’eux et grogne un peu.</p>\n<p>Saeko : C’était mon idée, p’tite tête.<br>\nYuu : Oh! Ce n’est pas bien de mentir~<br>\nFlynn : Allez tout le monde! On finit de tout ranger et on fout le camp d’ici! Kellan, tu sais quoi faire?<br>\nKellan : Ouaip!</p>\n<p>Kellan frappe ses poings l’un contre l’autre en souriant. Les Black Eagles et La Légion quittent rapidement les lieux se mettant à bonne distance de leur ancienne cachette. Seul Kellan reste dans le bâtiment et commence à détruire les poutres de soutien. Le bâtiment ne tarde pas à s’effondrer. Kellan vient les rejoindres quelques secondes plus tard en retirant la poussière qui lui était tombée dessus.</p>\n<p>Kellan : Une bonne chose de faite!<br>\nFlynn : Aller! On part! Tous dans les véhicules!</p>\n<p>Drake regarde Saeko, leur regard veut dire quelque chose, mais quoi. Tous montent dans leurs véhicules et partent en direction de leur base plus au nord-est du pays. Le groupe arrive à leurs nouveaux quartiers vers six heures du matin. Ils étaient tous épuisés et allèrent dormir.\nDu côté des employés de GenCorp, ils arrivent à l’entrepôt que Daryl avait indiqué. Un des gardes contacte Darakei pour l’avertir de l’état des lieux.</p>\n<p>Darakei : Oui?<br>\nSoldat : Bon matin, monsieur, nous venons d’arriver sur les lieux que Daryl nous avait indiqués, l’entrepôt s’est effondré. Nous allons procéder à une fouille et recherche d’indice.<br>\nDarakei : D’accord, procédez.</p>\n<p>Darakei raccroche en soupirant, lui qui voulait dormir. Il se décida à se lever, s’habiller et partir au bureau, sans oublier d’aller embrasser le front d’Éléa qui dormait encore.</p>"}},"chapitreAvant":"chapitre-13-fuite-pour-la-liberte","chapitreApres":"chapitre-15-hors-de-controle","nomRoman":"../le-premier-cyborg-tome-2","slug":"chapitre-14-l-inhibiteur"}},"pathContext":{"slug":"chapitre-14-l-inhibiteur"}}

